.admin-nav{display:flex;flex-direction:column;box-sizing:border-box;height:100vh;padding:.5rem;padding-top:3rem;background-color:#313131;width:12vw;min-width:200px}.admin-nav .admin-nav-item{padding-left:1.5rem;padding-right:1.5rem;padding-top:.5rem;padding-bottom:.5rem;border-radius:3px;display:flex;align-items:center;margin-bottom:10px;text-decoration:none;color:#f3f3f3}.admin-nav .admin-nav-item img{margin-right:.5rem;width:1.5rem}.admin-nav .admin-nav-item:hover{background-color:#3a3a3a}

.admin-home-main{width:100%}.admin-home-main h1{font-size:4rem;width:100%;margin-top:10rem;text-align:center}

.admin-login-form{position:fixed;top:40%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);width:30%;padding-top:2rem;padding-bottom:2rem;display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:white;border-radius:5px}.admin-login-form h1{margin-bottom:2rem}.admin-login-form .admin-login-input{display:flex;align-items:center;display:flex;flex-direction:row;justify-content:center;align-items:center;margin-bottom:1rem}.admin-login-form .admin-login-input input{margin-left:2rem;border:0;padding:.5rem;border-radius:5px;background-color:#f3f3f3}.admin-login-form .warn{color:#313131}.admin-login-form .submit{border-radius:10px;padding-left:2rem;padding-right:2rem;padding-top:.5rem;padding-bottom:.5rem;box-shadow:3px 3px 3px rgba(49,49,49,0.313725);font-size:large;background-color:#313131;border:none;color:#fff;transition-duration:.3s;border-radius:0;cursor:pointer;margin-top:1rem}.admin-login-form .submit:hover{background-color:#3a3a3a}

.admin-login .bg-img{position:fixed;top:0;left:0;background-image:url(/static/media/background.afe78909.jpg);background-size:cover;-webkit-filter:blur(8px);filter:blur(8px);width:100%;height:100%;z-index:-1;-webkit-transform:scale(1.1);transform:scale(1.1)}


.admin-user-main{width:100%}


.admin-post-main{width:100%}

.page{display:flex;flex-direction:row;overflow-y:hidden}.main-view{height:100vh;width:60vw}.map-view{height:100vh;width:40vw}.table-search{width:60%;display:flex;flex-direction:"row";justify-content:center;align-items:center;margin-left:auto;margin-right:auto;margin-top:1rem;margin-bottom:1rem}.table-search .table-search-input{height:1.5rem;border:none;font-size:1rem;background-color:#f3f3f3;width:100%;margin-right:.5rem;padding-left:1rem;padding-right:1rem;padding-top:.5rem;padding-bottom:.5rem}.table-search .table-search-input:focus,.table-search .table-search-input:active{box-shadow:3px 3px 3px rgba(49,49,49,0.313725)}.table-search .table-search-btn{padding-left:.5rem;padding-right:.5rem;padding-top:.5rem;padding-bottom:.5rem;box-shadow:0px 0px 0px rgba(49,49,49,0.313725);font-size:1rem;background-color:#313131;border:none;color:#fff;transition-duration:.3s;border-radius:0;cursor:pointer}.table-search .table-search-btn:hover{background-color:#3a3a3a}.admin-table{margin:auto;border-collapse:separate;border-spacing:0 .2rem;width:90%;overflow-x:auto}.admin-table th{text-align:left;padding:.5rem;background-color:#313131;color:#f3f3f3}.admin-table tbody tr{color:#313131}.admin-table tbody tr td{padding:.5rem;max-width:100px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.admin-table tbody tr .actions button{padding-left:5px;padding-right:5px;padding-top:.5rem;padding-bottom:.5rem;box-shadow:0 0 0 rgba(49,49,49,0.313725);font-size:normal;background-color:#313131;border:none;color:#fff;transition-duration:.3s;border-radius:0;cursor:pointer;margin-left:2px;margin-right:2px}.admin-table tbody tr .actions button:hover{background-color:#3a3a3a}.admin-table tbody tr:hover{box-shadow:#8a8a8a 0 0 1px}h1,h2,h3,h4,h5,h6{font-family:"Rubik","Lucida Sans","Lucida Sans Regular","Lucida Grande","Lucida Sans Unicode",Geneva,Verdana,sans-serif;font-weight:500}div,p,li,button{font-family:"Rubik","Lucida Sans","Lucida Sans Regular","Lucida Grande","Lucida Sans Unicode",Geneva,Verdana,sans-serif;font-weight:400}body{margin:0}

